// @import '~antd/lib/style/themes/default.less';
@import '../utils/utils.scss';

$screen-md              : 768px;
$screen-lg              : 992px;

.tableList {
  .tableListOperator {
    margin-bottom: 16px;
    button {
      margin-right: 8px;
    }
  }
}

.tableListForm {
  :global {
    .ant-form-item {
      margin-bottom: 24px;
      margin-right: 0;
      display: flex;
      > .ant-form-item-label {
        width: auto;
        line-height: 32px;
        padding-right: 8px;
      }
      .ant-form-item-control {
        line-height: 32px;
        height: 32px;
      }
    }
    .ant-form-item-control-wrapper {
      flex: 1;
    }
  }
  .submitButtons {
    display: block;
    white-space: nowrap;
    margin-bottom: 24px;
  }
}

// 操作列竖线
.operatorLink {
  margin-right: 16px;
  &:after {
    content: '';
    position: relative;
    top: -0.06em;
    right: -8px;
    box-sizing: border-box;
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 1px;
    height: 0.9em;
    line-height: 1.5;
    list-style: none;
    font-size: 14px;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
    font-variant: tabular-nums;
    color: rgba(0, 0, 0, 0.65);
    background: #e8e8e8;
    vertical-align: middle;
    cursor: default;
    pointer-events: none;
  }
  &:last-child {
    margin-right: 0;
    &:after {
      width: 0;
   }
  }
}

.operatorWrap:empty {
  &:after{
   content: '-';
   color :#999; 
  }

}

.wordBreak {
  word-break: break-all;
  word-wrap: break-word;
  display: inline-block;
}

@media screen and (max-width: $screen-lg) {
  .tableListForm :global(.ant-form-item) {
    margin-right: 24px;
  }
}

@media screen and (max-width: $screen-md) {
  .tableListForm :global(.ant-form-item) {
    margin-right: 8px;
  }
}
